$bg-color: red;
$text-color: white;

body{
    background-color: $bg-color;

    h1{
        color: $text-color;
    }
}

.example {
    display: grid;
    transition: all .5s;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    background: linear-gradient(to bottom, white, black);
}
